<template>
	<div class="footer">
		<router-link to="/index" tag="dl" class="footer_dl">
			<dt class="footer_dt conis1_active"></dt>
			<dd>首页</dd>
		</router-link>
		<router-link to="/class" tag="dl" class="footer_dl">
			<dt class="footer_dt conis2"></dt>
			<dd>分类</dd>
		</router-link>
		<router-link to="/shopCar" tag="dl" class="footer_dl">
			<dt class="footer_dt conis3"></dt>
			<dd>购物车</dd>
		</router-link>
		<router-link to="/myHome" tag="dl" class="footer_dl">
			<dt class="footer_dt conis4"></dt>
			<dd>我的</dd>
		</router-link> 
	</div>
</template>

<script>
	export default{
		mounted(){
			var odl = document.getElementsByClassName("footer_dl");
			var odt = document.getElementsByClassName("footer_dt");
			for(var i=0; i<odl.length; i++){
				let _index = i;
				odl[i].onclick = function(){
					for(var j=0; j<odt.length; j++){
						odt[j].className = "footer_dt conis"+(j+1);
					}
					odt[_index].className = "footer_dt conis"+(_index+1)+"_active";
				}
			}
		}
	}
</script>

<style lang="less" scoped>
	.footer{
		width: 100%;background: #fff;
		height: 0.9rem;
		border-top: 2px solid #e8e8e8;
		position: fixed;left: 0; bottom: 0;
		display: flex;justify-content: space-around;
		.footer_dl{
			width: 25%;
			height: 0.9rem;
			dt{
				width: 0.45rem;
				height: 0.45rem;
				margin: 0 auto;
				margin-top: 0.05rem;
			}
			.conis1{
				background: url(../../../assets/footer/tab_home_normal.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis2{
				background: url(../../../assets/footer/tab_category_normal.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis3{
				background: url(../../../assets/footer/tab_search_normal.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis4{
				background: url(../../../assets/footer/tab_my_normal.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis1_active{
				background: url(../../../assets/footer/tab_home_selected.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis2_active{
				background: url(../../../assets/footer/tab_category_pressed.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis3_active{
				background: url(../../../assets/footer/tab_search_selected.png);
				background-size: 0.45rem 0.45rem;
			}
			.conis4_active{
				background: url(../../../assets/footer/tab_my_selected.png);
				background-size: 0.45rem 0.45rem;
			}
			dd{
				text-align: center;
				line-height: 0.3rem;
				font-size: 0.18rem;
				color: #262626;
			}
		}
	}
</style>


