import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/pages/index'
import Class from '@/components/pages/class'
import ShopCar from '@/components/pages/shopCar'
import MyHome from '@/components/pages/myHome'

import JiaJuJiaFang from '@/components/pages/jiajujiafang'
import JiSheng from '@/components/pages/jisheng'
import MeiZhuang from '@/components/pages/meizhuang'
import Men from '@/components/pages/men'
import MvYin from '@/components/pages/mvyin'
import NeiYi from '@/components/pages/neiyi'
import ShiPin from '@/components/pages/shipin'
import ShuMa from '@/components/pages/shuma'
import SiPin from '@/components/pages/sipin'
import WoMen from '@/components/pages/women'
import XiangBao from '@/components/pages/xiangbao'
import XieXue from '@/components/pages/xiexue'
import YunDong from '@/components/pages/yundong'
import Goods from '@/components/pages/goods'
import Good from '@/components/pages/good'

import jieSuan from '@/components/pages/jieSuan'
import bianJi from '@/components/pages/bianji'
import gouMai from '@/components/pages/goumai'
import shouCang from '@/components/pages/shoucang'
import lianXi from '@/components/pages/lianxi'
import Quan from '@/components/pages/quan'
import bangZhu from '@/components/pages/bangzhu'
import ziLiao from '@/components/pages/ziliao'
import sheZhi from '@/components/pages/shezhi'

Vue.use(Router)

const routes = [
	{path:"/",redirect:"/index"},
	{path:"/index",component:Index},
	{	path:"/class",
		component:Class,
		children:[
			{path:"",redirect:"jiajujiafang"},
			{path:"jiajujiafang",component:JiaJuJiaFang},
			{path:"jisheng",component:JiSheng},
			{path:"meizhuang",component:MeiZhuang},
			{path:"men",component:Men},
			{path:"mvyin",component:MvYin},
			{path:"neiyi",component:NeiYi},
			{path:"shipin",component:ShiPin},
			{path:"shuma",component:ShuMa},
			{path:"sipin",component:SiPin},
			{path:"women",component:WoMen},
			{path:"xiangbao",component:XiangBao},
			{path:"xiexue",component:XieXue},
			{path:"yundong",component:YunDong}
		]
	},
	{path:"/shopCar",component:ShopCar},
	{path:"/myHome",component:MyHome},
	{path:"/class/jiajujiafang/goods/:id",component:Goods},
	{path:"/class/jiajujiafang/goods/good/:miao",component:Good},
	{path:"/shopCar/jieSuan",component:jieSuan},
	{path:"/shopCar/jieSuan/bianji",component:bianJi},
	{path:"/myHome/goumai",component:gouMai},
	{path:"/myHome/shoucang",component:shouCang},
	{path:"/myHome/lianxi",component:lianXi},
	{path:"/myHome/quan",component:Quan},
	{path:"/myHome/bangzhu",component:bangZhu},
	{path:"/myHome/ziliao",component:ziLiao},
	{path:"/myHome/shezhi",component:sheZhi}
]

export default new Router({
  routes,
  linkActiveClass:"ly-tab-active"
})



