					<ul class="ly-ul">
						<router-link class="ly-tab" v-for="(item,index) in btns" tag="li" :to="`/class/${item.url}`">
							{{item.name}}
						</router-link>
					</ul>
				</div>
				
				<div class="right">
					<router-view class="ly-right"></router-view>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "class",
		data() {
			return {
				btns:[
					{id:1,name:"家居家纺",url:"jiajujiafang"},
					{id:2,name:"女装",url:"women"},
					{id:3,name:"男装",url:"men"},
					{id:4,name:"内衣配饰",url:"neiyi"},
					{id:5,name:"运动户外",url:"yundong"},
					{id:6,name:"鞋靴",url:"xiexue"},
					{id:7,name:"箱包",url:"xiangbao"},
					{id:8,name:"食品酒饮",url:"shipin"},
					{id:9,name:"美妆个护",url:"meizhuang"},
					{id:10,name:"母婴",url:"mvyin"},
					{id:11,name:"饰品",url:"sipin"},
					{id:12,name:"数码家电",url:"shuma"},
					{id:13,name:"计生情趣",url:"jisheng"}
				]
			}
		}
	}
</script>

<style lang="less" scoped>
	.ly-top {
		width: 100%;
		height: 0.88rem;
		border-bottom: 0.01rem solid #cacaca;
		padding-left: 0.3rem;
		padding-right: 0.3rem;
		box-sizing: border-box;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 9999;
		background: #FFFFFF;
		input {
			outline: none;
			width: 100%;
			height: 0.64rem;
			border-radius: 0.3rem;
			border: none;
			background: #E8E8E8;
			margin-top: 0.12rem;
			padding-left: 0.71rem;
			box-sizing: border-box;
		}
		img {
			width: 0.34rem;
			height: 0.34rem;
			position: absolute;
			left: 0.5rem;
			top: 0.27rem;
		}
	}
	.ly-big {
		width: 100%;
		height: 100%;
		padding-top: 0.88rem;
		box-sizing: border-box;	
		.ly-big2{
			width: 100%;
			height: 100%;
			position: relative;
			.left{
				width: 25%;
				height: 100%;
				position: absolute;
				left: 0; top: 0;
				.ly-ul {
					width: 1.9rem;
					height: 100%;
					overflow:auto;
					.ly-tab {
						width: 1.9rem;
						height: 0.99rem;
						border-bottom: 0.01rem solid #FFFFFF;
						background: #f2f2f2;
						text-align: center;
						line-height: 1rem;
						color: #333333;
						font-size: 0.26rem;
					}
					.ly-tab-active{
						background: #fff;
					}
					.ly-tab:last-child{
						border-bottom: none;
					}
				}
			}
			.right{
				width: 75%;
				height: 100%;
				position: absolute;
				right: 0;top: 0;
				box-sizing: border-box;
				overflow: auto;
				.ly-right{
					width: 100%;
					height: auto;
				}
			}
		}	
	}
</style>


