<template>
	<div id="shoucang">
		<div class="shoucang">
			<header class="header">
				<span class="header_1"><img src="../../../assets/img/help/back_icon_unpressed.png" @click="fanhui()"/></span>
				<span class="header_2">我 的 收 藏</span>
				<span class="header_3"></span>
			</header>
		</div>
		<div class="mai">
			<span>您还没有收藏任何商品</span>
		</div>
	</div>
</template>

<script>
	export default {
		name: "shoucang",
		data() {
			return {}
		},
		methods:{
			fanhui(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style scoped>
	#shoucang {
		text-align: center;
	}
	.shoucang {
		width: 100%;
		height: auto;
		text-align: center;
	}
	.shoucang .header {
		height: 0.66rem;
		width: 100%;
		justify-content: space-between;
		display: flex;
		line-height: 0.66rem;
		border-bottom: 1px solid #d3d3d3;
	}
	.shoucang .header .header_1 {
		width: 0.44rem;
		height: 0.44rem;
		padding: 0.11rem;
	}
	.shoucang .header .header_1 img {
		width: 100%;
		height: 100%;
	}
	.shoucang .header .header_2 {
		font-family: "微软雅黑";
		font-weight: bold;
		font-size: 0.3rem;
	}
	.shoucang .header .header_3 {
		display: inline-block;
		width: 0.44rem;
		height: 0.44rem;
		padding: 0.11rem;
		font-weight: bold;
	}
	#shoucang .mai {
		padding-top: 4rem;
		font-size: 0.3rem;
		font-weight: bold;
	}
</style>


