<template>
	<div class="quan">
		<header class="header">
			<span class="header_1"><img @click="fanhui()" src="../../../assets/img/personal/back_icon_unpressed.png"/></span>
			<span class="header_2">我的优惠券</span>
			<span class="header_3">说明</span>
		</header>
		<div class="main">
			<p class="m_1"><img src="../../../assets/img/coupon/empty_coupon.png" /></p>
			<p class="m_2">优惠券会有的,一切都会有的~</p>
		</div>
	</div>
</template>

<script>
	export default {
		name: "quan",
		data() {
			return {}
		},
		methods:{
			fanhui(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style scoped="scoped">
	.quan {
		width: 100%;
		height: 100%;
		text-align: center;
	}
	
	.quan .header {
		height: 0.66rem;
		width: 100%;
		justify-content: space-between;
		display: flex;
		line-height: 0.66rem;
		border-bottom: 1px solid #d3d3d3;
	}
	
	.quan .header .header_1 {
		width: 0.44rem;
		height: 0.44rem;
		padding: 0.11rem;
	}
	
	.quan .header .header_1 img {
		width: 100%;
		height: 100%;
	}
	
	.quan .header .header_2 {
		font-size: 0.26rem;
		font-weight: bold;
	}
	
	.quan .header .header_3 {
		width: 0.74rem;
		font-size: 0.26rem;
		margin-left: 0.11rem;
	}
	
	.quan .main {
		margin-top: 3rem;
		width: 100%;
		height: 1.5rem;
	}
	
	.quan .main .m_2 {
		width: 100%;
		height: 0.3rem;
		margin-top: 0.3rem;
		font-size: 0.2rem;
		display: inline-block;
	}
	
	.quan .main .m_1 {
		width: 1rem;
		height: 0.78rem;
		display: inline-block;
	}
	
	.quan .main .m_1 img {
		width: 100%;
		height: 100%;
	}
</style>


