<template>
	<div id="goods">
		<header class="header">
			<div class="top">
				<img src="../../../../static/class/goods/back_icon_unpressed.png" class="l_t" @click="goback()"/>
				<span>Nike</span>
				<div class="r_t">筛选</div>
			</div>
			<div class="bottom">
				<div class="b_le">
					<i>排序</i>
					<ul>
						<li>综合排序</li>
						<li>价格最低</li>
						<li>价格最高</li>
					</ul>
				</div>
				<div class="b_rg">
					<i>分类</i>
					<ul>
						<li>11111</li>
						<li>33333</li>
						<li>55555</li>
					</ul>
				</div>
			</div>
		</header>
		<div class="main1">
			<router-link tag="dl" class="ctt" v-for="(item,index) in list" :to="`/class/jiajujiafang/goods/good/${item.miao}`" v-if="item.id == $route.params.id">
				<dt><img :src="item.url"/></dt>
				<dd>{{item.miao}}</dd>
				<dd>{{item.jiage}}</dd>
			</router-link>
		</div>
	</div>
</template>

<script>
	export default{
		name:"goods",
		data(){
			return{
				list:[],
				id:""
			}
		},
		methods:{
			goback(){
//				history.back();
				this.$router.go(-1);
			}
		},
		mounted(){
			this.id = this.$route.params.id
			this.$http.get('http://localhost:8080/static/data.json').then((res)=>{
				this.list = res.data.goods;
			},(err)=>{
				console.log("请求失败")
			})
			$("i").toggle(function(){
				$(this).css({"color":"#f00","background-image":"url(../../../../static/class/goods/drop_down_selected_icon.png)"});
				$(this).next().slideDown()
			},function(){
				$(this).css({"color":"#000","background-image":"url(../../../../static/class/goods/drop_down_unselected_icon.png)"});
				$(this).next().slideUp()
			})
		}
	}
</script>

<style lang="less" scoped>
	#goods{
		width: 100%;
		height: 100%;
		box-sizing: border-box;
		position: relative;
		.header{
			width: 100%;
			height: auto;
			.top{
				width: 100%;
				height: 0.88rem;
				border-bottom: 1px solid #cccccc;
				position: relative;
				.l_t{
					width: auto;
					height: 100%;
					position: absolute;
					left: 0.08rem;
					top: 50%;
					transform: translateY(-50%);
				}
				.r_t{
					position: absolute;
					right: 0.3rem;
					top: 50%;
					transform: translateY(-50%);
					font-size: 0.26rem; color: #000;
				}
				span{
					display: block;
					line-height: 0.88rem;
					text-align: center;
					font-size: 0.26rem; color: #000;
				}
			}
			.bottom{
				width: 100%;
				height: 0.8rem;
				border-bottom: 1px solid #cccccc;
				display: flex;
				justify-content: space-around;
				position: relative;
				div{
					width: 50%;
					height: 100%;
					line-height: 0.8rem;
					text-align: center;
					font-size: 0.26rem;
					i{
						display: block;
						width: 100%;
						height: 100%;
						background: url(../../../../static/class/goods/drop_down_unselected_icon.png) no-repeat;
						background-position: 65% 50%;
						background-size: 0.30rem 0.15rem;
						color: #000;
					}
					ul{
						width: 100%;
						height: auto;
						position: absolute;
						left: 0;
						top: 100%;
						z-index: 999;
						display: none;
						background: #fff;
						li{
							width: 100%;
							line-height: 0.8rem;
							text-align: left;
							box-sizing: border-box;
							padding-left: 0.60rem;
						}
					}
				}
			}
		}
		.main1{
			width: 100%;
			height: auto;
			position: absolute;
			top: 1.73rem; bottom: 0;
			overflow: auto;
			display: flex;
			justify-content: flex-start;
			flex-wrap: wrap;
			.ctt{
				width: 50%;
				height: 5.1rem;
				dt{
					width: 100%;
					height: 3.7rem;
					img{
						width: 100%;
						height: 100%;
					}
				}
				dd{
					width: 100%;
					height: 0.5rem;
					line-height: 0.5rem;
					color: #000;
					text-align: center;
				}
				dd:first-child{
					font-size: 0.26rem;
				}
				dd:last-child{
					font-size: 0.22rem;
				}
			}
		}
	}
</style>


